import React, { Component } from 'react';
import logo from './logo.svg';
import Gallery from 'react-photo-gallery';
import Lightbox from 'react-images';

import './App.css';

const photos = [
  { src: 'https://farm2.staticflickr.com/1823/42701655814_f48c6c9bfc_k_d.jpg', width: 3, height: 2 },
  { src: 'https://farm5.staticflickr.com/4494/37187652144_60bdbc075c_k_d.jpg', width: 3, height: 2 },
  { src: 'https://farm2.staticflickr.com/1581/26541533722_ba3b9152d1_k_d.jpg', width: 3, height: 2 },
  { src: 'https://farm8.staticflickr.com/7316/16515498345_436ea19086_k_d.jpg', width: 3, height: 2 },
  { src: 'https://farm9.staticflickr.com/8659/16415641542_b4292d66cf_k_d.jpg', width: 4, height: 1 },
  { src: 'https://instagram.fchc2-1.fna.fbcdn.net/vp/feaad68040adec854cd9afc185b487d9/5BCAFD51/t51.2885-15/e15/11419084_914648818596429_1965366029_n.jpg', width: 1, height: 1 },
  { src: 'https://instagram.fchc2-1.fna.fbcdn.net/vp/81b252a54e2d4a523c59b5b56ccf339b/5BDB2231/t51.2885-15/e35/19227745_1724029361228233_1191721805367214080_n.jpg', width: 1, height: 1 },
  { src: 'https://instagram.fchc2-1.fna.fbcdn.net/vp/54aa1132718e0589bf2120f88f477caa/5BC61374/t51.2885-15/e35/12728462_1549828425329709_918070912_n.jpg', width: 1, height: 1 },
  { src: 'https://instagram.fchc2-1.fna.fbcdn.net/vp/cb265f8dcc22fef1b2b1b716e9346003/5BE8481D/t51.2885-15/e35/15275591_1789506484600001_2908048293705547776_n.jpg', width: 1, height: 1 },
  { src: 'https://farm9.staticflickr.com/8595/15867756950_55ad8716dc_k_d.jpg', width: 3, height: 2 },
  { src: 'https://farm8.staticflickr.com/7464/15840743789_d7893bf954_k_d.jpg', width: 3, height: 2 }
];


class App extends React.Component {
  constructor() {
    super();
    this.state = { currentImage: 0 };
    this.closeLightbox = this.closeLightbox.bind(this);
    this.openLightbox = this.openLightbox.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.gotoPrevious = this.gotoPrevious.bind(this);
  }
  openLightbox(event, obj) {
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
    });
  }
  closeLightbox() {
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false,
    });
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1,
    });
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1,
    });
  }
  render() {
    return (
      <div>
        <Gallery photos={photos} onClick={this.openLightbox} />
        <Lightbox images={photos}
          onClose={this.closeLightbox}
          onClickPrev={this.gotoPrevious}
          onClickNext={this.gotoNext}
          currentImage={this.state.currentImage}
          isOpen={this.state.lightboxIsOpen}
        />
      </div>
    )
  }
}

export default App;



